import React, { useContext } from 'react';
import { Link } from 'react-router-dom';
import { AccountContext } from '../../context/AccountContext';
import "./GameBox.css"

const GameBox = ({game,main}) => {

    const {xp,balance} = useContext(AccountContext);
    const isLock=()=>{return !isBalanceOk() || !isXpOk()}
    const isXpOk=()=>{return game.minXp<=xp}
    const isBalanceOk =()=>{return game.enterPrice<=balance}
    return ( 
        <div className={"forth-color "+(main?"main-box":"box")}>
            {isLock() && <span className='info noselect tooltip' role="img" aria-label="lock">🔒
                <span className="tooltiptext">{!isXpOk() ?game.minXp+"xp +":"not enough money" }</span>
            </span>}
            
            <Link className={main?"main-head-font":"head-font"} style={{"textDecoration":"none"}}  to={!isLock()?"/waiting-room/"+game.id:"#"}>
                <div className={"third-color "+(main?"main-head":"head")}>
                    {game.name}
                    <div>{game.enterPrice}$</div>
                </div>
            </Link>
            <div className={"foot noselect " + (main?"main-foot-font":"foot-font")}>
                <div>{game.bids[0]}...{game.bids[game.bids.length-1]}</div>
                <div>{game.type}</div>
            </div>
        </div>
     );
}
 
export default GameBox;