import React from 'react';
import { CodeContext } from '../context/CodeContext';
import { DialogContext } from '../context/DialogContext';

class Name extends React.Component
{
    constructor(props) {
        super(props);
        this.state = {name: ""};
    }

    componentDidMount()
    {
        if(this.props.myname) this.setState({name:this.getMyName()})
        else this.setState({name: this.getRandomName(true)})
    }

    getRandomName = (includeCustom)=>
    {
        if(includeCustom && this.getRandom(3)!==1) return customNames[this.getRandom(customNames.length)];
        return "Player"+this.getRandom(1000);
    }

    getRandom(range)
    {
        return Math.floor( Math.random()*range);
    }

    getMyName= ()=> {
        let name = localStorage.getItem("name");
        if(!name) 
        {
            name = this.getRandomName(false);
        }

        return name;
    }

    setMyName = (name)=> {
        if(!localStorage.getItem("name"))
            this.props.codeContext.saveCode(1,"Congrats you set your name!")
            
        localStorage.setItem("name",name);
        this.setState({name:name})
    }
    
    handleOnClick = ()=>{
        this.context.setInputDialog("Change your name",(name)=>{if(name && name!=="")this.setMyName(name)});
    }

    /*
    convert(string)
    {
        if(string.length>10) return string.slice(0,7)+"..."
        return string;
    }
    */

    render()
    { 
        return (
            <ins className={this.props.clickable?"hoverable underline":""} style={{"fontWeight":(this.props.bold?"bold":"none")}}
            onClick={()=>{if(this.props.clickable)this.handleOnClick()}} >
                {this.state.name}
            </ins>
        );
    }
    

}
Name.contextType = DialogContext;

export const withCodeContext = Component => (
    props => (
      <CodeContext.Consumer>
        {context => <Component codeContext={context} {...props} />}
      </CodeContext.Consumer>
    )
  )

export default withCodeContext(Name);

