export class Game {
  constructor(id,name,rank,firstbid,lastbid,type,minXp,roundTime=30) {
    this.id = id;
    this.rank = rank;
    this.name = name;

    //hand>=table
    this.handDeckSize = 5;//1-10
    this.tableDeckSize = 5;//1-10

    this.bids = new Array(this.tableDeckSize).fill(firstbid);
    this.bids[this.tableDeckSize-1]=lastbid;
    
    this.type = type;    
    this.minXp = minXp;
    this.roundTime= roundTime;//in seconds, must be odd number
    
    this.enterPrice = this.bids.reduce((a, b) => a + b, 0)/2;
  }
}

export const GameType = {
  HiddenCards:"HIDDEN",
  OpenCards:"OPEN",
};

export const Rank={
  Beginner:"BEGINNER",
  Gamer:"Gamer",
  Pro:"PRO",
}
