import React, { useContext }  from 'react';
import { useParams } from 'react-router';
import { Link } from 'react-router-dom';
import { DialogContext } from '../context/DialogContext';
import { OnlineContext } from '../context/OnlineContext';
import { GameSet } from '../models/consts';
import style from './local.css'

const WaitingRoom = () => {

    let { id } = useParams();

    let game = GameSet.getGame(id);

    const {gamePlayers} = useContext(OnlineContext);
    
    const {isDialog} = useContext(DialogContext);

    return ( 
        <div className={"blurable " + (isDialog?"blured":"") + " layout"} >
            <div className={style.content}>
                <div className='fade'>
               
                <div>Select color:</div>
                <div className={style.flex}>
                <div className={style.foot}>
                        <div>{gamePlayers.red}</div>
                        <span role="img" aria-label="^" >👤</span>
                        <div>{gamePlayers.black}</div>
                    </div>
                    <div className={style.head}>
                        <Link to={{ pathname: ('/game/'+game.id+"/red")}}>
                            <span role="img"aria-label="^" style={{"left":"10px","top":"0px"}}>♥️</span>
                            Red
                            <span role="img"aria-label="^" style={{"right":"10px","bottom":"0px"}}>♦️</span>
                        </Link>
                        <Link to={{ pathname: ('/game/'+game.id+"/black")}}>
                            <span role="img" aria-label="^" style={{"left":"10px","top":"0px"}}>♠️</span>
                            Black
                            <span role="img" aria-label="^" style={{"right":"10px","bottom":"0px"}}>♣️</span>
                        </Link>
                    </div>
                
                    
                </div>
                </div>
            </div>

            <div  >
                <h2>{game.name}</h2>
                <div >
                    <div>Enter price: {game.enterPrice}$</div>
                    <div>Bids range: {game.bids[0]}$-{game.bids[game.bids.length-1]}$</div>
                    <div>Game type: {game.type}</div>
                    <div>Round time: {game.roundTime}s</div>
                </div>
            </div>
        </div>
     );
}

export default WaitingRoom;