import React, { useContext, useEffect } from 'react';

import Name from '../components/Name';
import GameBox from './components/GameBox';

import style from "./Home.css"
import { NotificationContext } from '../context/NotificationContext';
import { DialogContext } from '../context/DialogContext';
import { GameType, Rank } from '../models/game';
import classNames from 'classnames';
import { GameSet } from '../models/consts';
import { CodeContext } from '../context/CodeContext';


const Home = () => {

    const {isNotification,showNotification} = useContext(NotificationContext);
    const {saveCode,isCodeUsed} = useContext(CodeContext);
    const {isDialog} = useContext(DialogContext);

    const currRank=Rank.Beginner;
    const currGames=GameSet.getGames(currRank);

    useEffect(()=>{
        let timeout = setTimeout(() => {  
            if(!isCodeUsed(0)) saveCode(0,"Hello new player! Ready to start?");
        }, 8000);
        return ()=>clearTimeout(timeout);
    },[])

    useEffect(()=>{
        if(isNotification){
            let timeout = setTimeout(()=>showNotification(),1000);
            return ()=>clearTimeout(timeout)
        } 
    },[isNotification])

    return (
    <div>
        <div className={"blurable fade " +classNames({"blured":isDialog})}>
        <h1 style={{'textAlign':'center',marginTop:"50px"}} >Hello,<Name myname clickable/>!</h1>
        
        <div className={style.flex}>
            
            <div className={style.margin}>
                200$+
                <div className={style.margin}>
                    <GameBox game={currGames[2]}/>
                    <GameBox game={currGames[3]}/>
                </div>
            </div>
            <div className={style.margin} style={{display:"flex",justifyContent:"center",alignItems:"center",flexDirection:"column"}}>
                <GameBox game={currGames[0]} main />
                <GameBox game={currGames[1]} />
            </div>
            
            <div className={style.margin}>
                1000$+
                <div className={style.margin}>
                    <GameBox game={currGames[4]}/>
                    <GameBox game={currGames[5]}/>
                </div>
            </div>
        </div>
        </div>

        <div className={style.rank}>CURRENT RANK: {currRank}</div>
    </div>
    );
}
 
export default Home;