import { Game, GameType, Rank } from "./game";

export const GameSet = {
    games:[
      new Game(0,"PLAY",Rank.Beginner,10,20,GameType.HiddenCards,0),
      new Game(1,"LATER",Rank.Beginner,20,80,GameType.HiddenCards,100),
      new Game(2,"EQUAL",Rank.Beginner,100,100,GameType.HiddenCards,300),
      new Game(3,"FAST",Rank.Beginner,200,400,GameType.HiddenCards,500),
      new Game(4,"OPEN",Rank.Beginner,300,1200,GameType.OpenCards,900),
      new Game(5,"PRO",Rank.Beginner,1000,1000,GameType.OpenCards,1000),
    ],
    getGame:(id)=>GameSet.games[id],
    getGames:(rank)=>GameSet.games.filter(game=>game.rank===rank)
}